@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

h1, h2, h3, h4, h5, h6 {
	padding: 0;
	@include font-normal;
	color: $title-color;
	margin: 0 0 $bottom-space;

	@include media('<mob-lg') {
		margin-bottom: round-float($bottom-space*0.85, 1); }

	&:last-child {
		margin-bottom: 0; }

	a {
		text-decoration: none;
		color: inherit;

		.no-touchevents &:hover {
			color: $base-color;
			text-decoration: underline; } } }

h1 {
	@include font-h1; }

h2 {
	@include font-h2; }

h3 {
	@include font-h3; }

h4 {
	@include font($font-size-h4, $line-height-h4); }

h5 {
	@include font($font-size-h5, $line-height-h5); }

h6 {
	@include font($font-size-h6, $line-height-h6);
	text-transform: uppercase; }

p {
	padding: 0;
	margin: 0 0 $bottom-space; }

hr {
	line-height: 1px;
	height: 0;
	border: none;
	background: none;
	border-bottom: 1px solid $border-color;
	margin-bottom: $bottom-space; }

a {
	color: $link-color;
	text-decoration: underline;
	cursor: default;
	//outline: none

	&:link {
		cursor: pointer; }

	.no-touchevents &:hover {
		text-decoration: none; }

	&[href^="tel"], .no-touchevents &[href^="tel"]:hover {
		text-decoration: none !important;
		color: inherit;
		cursor: default; }

	&[href^="javascript:"] {
		cursor: default; } }

img {
	border: none;
	vertical-align: top;
	max-width: 100%;

	table & {
		max-width: none; } }

b, strong, th {
	@include font-bold; }

i, em {
	@include font-italic; }

i b, i strong, em b, em strong, b i, b em, strong i, strong em {
	@include font-bold-italic; }

.mark {
	color: $base-color;
	//font-style: inherit
 }	//font-weight: inherit

.text-color-alt {
	color: $base-color; }

.align-center {
	text-align: center; }

.align-left {
	text-align: left; }

.align-right {
	text-align: right; }

.float-left {
	float: left; }

.float-right {
	float: right; }

.nobr {
	white-space: nowrap; }

.hide-text {
	@include hide-text; }

/*content img */
.imgc {
	text-align: center;
	display: block;
	margin: 0 auto $bottom-space;
	width: $flood-layout-width;
	max-width: 100%;
	clear: both; }

.imgl {
	text-align: left;
	float: left;
	margin: 0 $bottom-space $bottom-space 0; }

.imgr {
	text-align: right;
	float: right;
	margin: 0 0 $bottom-space $bottom-space; }

.imgl, .imgr {
	max-width: 40%;

	@include media('<tab-sm') {
		float: none;
		display: block;
		margin-left: auto;
		margin-right: auto;
		max-width: 100%; } }

.imgc, .imgl, .imgr {
	figcaption {
		display: block;
		padding: $bottom-space 0 0;
		margin: 0;
		@include font($font-size--small, $line-height--small); } }

/*content img end */

.#{$user-content} {
	@each $tags in h2, h3, h4, h5 {
		p + #{$tags},
		ul + #{$tags},
		ol + #{$tags},
		hr + #{$tags},
		table + #{$tags},
		.table-auto + #{$tags} {
			margin-top: $bottom-space * 2; } } }

/*table */
table {
	border-collapse: collapse;
	border-spacing: 0;
	width: auto;
	margin-bottom: $bottom-space;
	border-color: currentColor;

	td, th {
		@include font($font-size, $line-height);
		color: inherit;
		padding: 8px 15px;
		border-color: currentColor; } }

.table-auto {
	position: relative;
	overflow: auto;
	max-width: 100%;
	-webkit-overflow-scrolling: touch;
	margin-bottom: $bottom-space;

	table {
		margin-bottom: 0;
		width: auto;
		min-width: 100%; } }

/*table end */
