// =============================================================================
// pow
// =============================================================================
///   
///
/// @param {Number, Length} $number - 
/// @param {Number, Length} $exponent - 
/// @return {Number} -  `$number`    `$exponent`

@function pow($number, $exponent) {
	$value: 1;

	@if $exponent > 0 {
		@for $i from 1 through $exponent {
			$value: $value * $number; } }

	@return $value; }

// =============================================================================
// round-float
// =============================================================================
///   n   
///
/// @param {Number, Length} $number - 
/// @param {Number, Length} $n -    
/// @return {Number} -    `n`   

@function round-float($number, $n) {
	$float: pow(10,$n);

	$value: round($number*$float)/$float;

	@return $value; }
