@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

html {
	font-size: 62.5%;

	//  
	//&.css-scroll-fixed:not(.open-only-mob)
	//include scroll-blocked

	//&.css-scroll-fixed.open-only-mob
	//include media('<tab-lg')
 }	//@include scroll-blocked

body {

	position: relative;
	@include font-normal;
	@include font($font-size, $line-height);
	color: $text-color;
	min-width: $page-layout-min;
	background: $bg-color url(../../img/bg-desktop.png) no-repeat center 21px fixed;

	//  
	//*::selection
	//ackground-color: $base-color
	//olor: white

	//1.5 dpr
	@media(-webkit-min-device-pixel-ratio: 1.5), (min-resolution: 144dpi) {
		background-image: url(../../img/bg-desktop@2x.png);
		background-size: round(2136 / 2) * 1px; } }

/* === LAYOUT === */
.max-wrap {
	max-width: $page-layout-width;

	@include media('>tab-sm') {
		max-width: $page-layout-width-desk; } }

.layout {
	@include padding-horizontal(nth($page-layout-space, 2));
	@include push-auto; }

.wrapper-flex {
	display: flex;
	flex-direction: column;
	flex-grow: 1;
	flex-shrink: 1;
	align-items: stretch; }

.wrapper {
	position: relative;
	padding-top: $header-height;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: stretch;
	margin: 0 auto;
	width: 100%;
	min-height: 100vh;
	max-width: $page-layout-width;
	box-shadow: 0 2px 4px rgba($shadow-color, 0.5);

	@include media('>tab-sm') {
		max-width: $page-layout-width-desk; }

	.home-page & {
		padding-top: 0; } }

/* === LAYOUT end === */

/* === CONTENT === */
.main {
	position: relative;
	display: flex;
	flex-direction: column;
	flex-grow: 1;

	.home-page & {
		padding-top: 3.6rem;
		padding-bottom: 6.3rem; } }

/* === CONTENT end === */

/* === HEADER === */
.header {
	position: relative;
	height: $header-height; }

.header_fixed {
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	z-index: 5; }

.header__holder {
	height: 100%;
	background-color: $bg-color;
	box-shadow: 0 2px 12px rgba($shadow-color, 0.5);
	@include padding-horizontal(nth($page-layout-space, 2));
	margin: 0 auto;
	max-width: $page-layout-width;

	@include media('>tab-sm') {
		max-width: $page-layout-width-desk; } }

/* === HEADER end === */

/* === FOOTER === */
.footer {
	position: relative; }

.footer_float {
	position: fixed;
	left: 0;
	bottom: 0;
	width: 100%;
	z-index: 5; }

.footer__holder {
	height: 100%;
	background-color: $bg-color;
	box-shadow: 0 -2px 12px rgba($shadow-color, 0.5);
	@include padding-horizontal(nth($page-layout-space, 2));
	margin: 0 auto;
	max-width: $page-layout-width;

	@include media('>tab-sm') {
		max-width: $page-layout-width-desk; } }

/* === FOOTER end === */
